* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  height: 100%; /* to get footer on the bottom */
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  display: flex; /* to get footer on the bottom */
  flex-direction: column; /* to get footer on the bottom */
  min-height: 100%; /* to get footer on the bottom */
}

footer {
  margin-top: auto; /* to get footer on the bottom */
}